    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                shopName: ["全部","请假","外出","出差","加班","换班","补卡"],
                prototype: [
                    { id:0,txt:'全部' },
                    { id:1,txt:'请假' },
                    { id:2,txt:'外出' },
                    { id:3,txt:'出差' },
                    { id:4,txt:'加班' },
                    { id:5,txt:'换班' },
                    { id:6,txt:'补卡' },
                ],
                show: false,  // 判断筛选弹窗显示或隐藏
                company_name: "",  // 类型
                causeList: [],
                typeId: 0,
            }
        },
        mounted(){
            this.getInfo();
        },
        methods:{
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.applyLog,
                    type: "post",
                    data: {
                        type: this.typeId
                    },
                    success: (response, allResponse) => {
                        this.causeList = response.list
                    }
                });
            },
//            头部导航右侧文字按钮
            navRigth(){
                this.show = true;
            },
//            筛选弹窗确定按钮
            choice(){
                this.show = false;
                this.prototype.forEach((item,index)=>{
                    if(this.company_name == item.txt){
                        this.typeId = item.id
                    }
                })
                this.getInfo();
            },
        }
    }



